export function amap(resolve, reject, options = {}) {
    const AMap = window.AMap;
    const mapObj = new AMap.Map('iCenter');
    mapObj.plugin('AMap.Geolocation', function () {
        const geolocation = new AMap.Geolocation({
            enableHighAccuracy: true,//:true
            timeout: options.timeout || 10000,          //10
            maximumAge: 1000,           //00
            convert: true,           //true
            showButton: true,        //true
            buttonPosition: 'LB',    //'LB'
            buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
            showMarker: true,        //true
            showCircle: true,        //true
            panToLocation: true,     //true
            zoomToAccuracy: true      //false
        });
        geolocation.getCurrentPosition((status, result) => {
            if (status === 'complete') {
                result.latitude = result.position.lat;
                result.longitude = result.position.lng;
                resolve(result);
            } else {
                reject(result);
            }
        });
    });
}



