"use strict";
import Cmd from './lib/cmd';
import * as util from './lib/util';

/*****************************
 * 
 *****************************/
const cmd = new Cmd();
const sysinfo = util.getSysInfo();
const doc = document;
const win = window;

if (doc) {
    cmd.setObj({
        domain: doc.domain,
        url: doc.URL || location.href,
        title: doc.title,
        referrer: doc.referrer || ''
    });
}
if (win && win.screen) {
    cmd.setObj({
        height: win.screen.height || win.outerHeight || 0,
        width: win.screen.width || win.outerWidth || 0,
    });
}
if (navigator) {
    cmd.set('lang', navigator.language || '');
}
cmd.setObj(sysinfo).setObj({
    deviceId: util.getDeviceID(),
    ua: navigator.userAgent
});

export default cmd;
